import './App.css';
import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardHeader from '@mui/material/CardHeader';
import CssBaseline from '@mui/material/CssBaseline';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import GlobalStyles from '@mui/material/GlobalStyles';
import Container from '@mui/material/Container';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import { useState, useEffect } from 'react';



const timer = {
    title: '25 + 5 Clock',
    breakLable: 'Break Length', 
    sessionLable: 'Session Length',
    buttonText: 'Sign up for free',
    buttonVariant: 'outlined',
  }



function PricingContent() {
  const [ breakLength, setBreakLength ] = useState(5)
  const [ sessionLength, setSessionLength ] = useState(25)
  const [ sessionFlag, setSessionFlag ] = useState(true)
  const [ timeLeft, setTimeLeft ] = useState( sessionLength )
  const [ isCounting, setIsCounting ] = useState( false )
  const [ reseted, setReseted ] = useState( true )

  const minutes = Math.floor(timeLeft / 60).toString().padStart(2, 0)
  const seconds = (timeLeft - minutes * 60).toString().padStart(2, 0)

  useEffect(() => {
    const audio = document.getElementById("beep");
    if (timeLeft === 0) audio.play()
    // if (timeLeft === sessionLength * 60) {
    //   audio.pause() 
    //   //audio.currentTime = 0
    // }
    const interval = setInterval(() => {
      isCounting && 
      setTimeLeft(timeLeft => (timeLeft >= 1 ? timeLeft - 1 : 0))
    }, 1000);
    if (timeLeft === 0) {
      //setIsCounting(false)
      setSessionFlag(!sessionFlag)
      setTimeLeft(!sessionFlag ? sessionLength * 60: breakLength * 60)
    } 
    !isCounting && reseted && setTimeLeft(sessionFlag ? sessionLength * 60: breakLength * 60)
    return() => {
      clearInterval(interval)
    }


  }, [isCounting, timeLeft, breakLength, sessionFlag, sessionLength])

  const handleStart = () => {
    setIsCounting(true)
    setReseted(false)
  }
  const handleStop = () => {
    setIsCounting(false)
    setReseted(false)
  }
  const handleReset = () => {
    setReseted(true)
    setIsCounting(false)
    setTimeLeft(25 * 60)
    setSessionLength(25)
    setBreakLength(5)
    setSessionFlag(true)
    const audio = document.getElementById("beep");
    audio.pause()
    audio.currentTime = 0
    
    
  }

  const buttonHandler = event => {
    console.log(event.target.id)
    switch(event.target.id) {
      case "break-increment":
        setBreakLength(breakLength => (breakLength >= 60 ? 60 : breakLength + 1 ))
        break
      case "break-decrement": 
        setBreakLength(breakLength => (breakLength <= 1 ? 1 : breakLength - 1 ))
        break
      case "session-increment":
        setSessionLength(sessionLength => (sessionLength >= 60 ? 60 : sessionLength + 1 ))
        break
      case "session-decrement":
        setSessionLength(sessionLength => (sessionLength <= 1 ? 1 : sessionLength - 1 ))
        break  
      default :
        console.log("there was an error")
    }
  }

  return (
    <React.Fragment>
      <GlobalStyles styles={{ ul: { margin: 0, padding: 0, listStyle: 'none' } }} />
      <CssBaseline />
      
      <Container maxWidth="md" component="main">
        <Grid container spacing={5} alignItems="flex-end">
            <Grid
              item
              key={timer.title}
              xs={12}
              sm={timer.title === 'Enterprise' ? 12 : 6}
              md={4}
            >
              <Card>
                <CardHeader
                  title={timer.title}
                />
                <CardContent>
                  <Box
                    sx={{
                      display: 'flex',
                      justifyContent: 'center',
                      alignItems: 'baseline',
                      mb: 2,
                    }}
                  >
                    <Typography id="break-label" variant="h6">
                      {timer.breakLable}
                      <Button id="break-increment" onClick={buttonHandler}><ArrowUpwardIcon/></Button>
                      <Button id="break-decrement" onClick={buttonHandler}><ArrowDownwardIcon/></Button>
                      <Typography id="break-length">{breakLength}</Typography>
                    </Typography>
                    <Typography id="session-label" variant="h6">
                      {timer.sessionLable}
                      <Button id="session-increment" onClick={buttonHandler}><ArrowUpwardIcon/></Button>
                      <Button id="session-decrement" onClick={buttonHandler}><ArrowDownwardIcon/></Button>
                      <Typography id="session-length">{sessionLength}</Typography>
                      
                    </Typography>
                  </Box>
                </CardContent>
                <CardActions>
                  <Typography id="timer-label">{sessionFlag ? "Session" : "Break"}</Typography>
                  <Typography id="time-left" >{`${minutes}:${seconds}`}</Typography>
                  { isCounting ? 
                  <Button  id="start_stop" onClick={handleStop}>Stop</Button> :
                  <Button  id="start_stop" onClick={handleStart}>Start</Button> }
                   
                  <Button id="reset" onClick={handleReset}>Reset</Button>
                  {isCounting}
                  
                  
                </CardActions>
              </Card>
            </Grid>
          </Grid>
      </Container>
    </React.Fragment>
    
  );
}
function App() {
  return (
    <PricingContent />
  );
}

export default App;
